<template>
  <div>
    <b-navbar toggleable="lg" type="dark" variant="info">
      <b-container>
        <b-navbar-brand href="#">PrimeBazaar</b-navbar-brand>

        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

        <b-collapse id="nav-collapse" is-nav>
          <b-navbar-nav>
            <b-nav-item to="/dealsoftheday">Deals of the day</b-nav-item>
          </b-navbar-nav>

          <!-- Right aligned nav items -->
          <b-navbar-nav class="ml-auto">
            <b-nav-item>
              <b-button
                variant="outline-light"
                size="sm"
                @click="$bvModal.show('bv-modal-example')"
              >
                <i class="fas fa-shopping-cart d-none d-md-inline"></i>
                <span class=""> My Cart ({{ cart.length }})</span>
              </b-button>
            </b-nav-item>
          </b-navbar-nav>
        </b-collapse>
      </b-container>
    </b-navbar>

    <!--Modal of Cart Section -->
    <div>
      <!-- Cart Modal -->
      <b-modal
        id="bv-modal-example"
        scrollable
        hide-footer
        title-class="font-weight-bold text-center"
        class="custom-modal"
      >
        <!-- Custom Modal Title -->
        <template #modal-title>
          <h4 class="text-dark mb-0">
            My <code>Cart</code> | ({{ cart.length }}) Items
          </h4>
        </template>

        <!-- Cart Items List -->
        <div class="cart-items-list">
          <!-- Display Cart Items if available -->
          <div v-if="cart.length">
            <!-- Iterate through cart items -->
            <b-card
              v-for="(cartItem, idx) in cart"
              :key="idx"
              class="my-3 shadow-sm rounded-lg cart-item-card border-dark"
            >
              <!-- Row layout for Image and Details -->
              <div class="d-flex align-items-start">
                <!-- Product Image -->
                <b-img
                  :src="cartItem.img"
                  alt="Product image"
                  fluid
                  class="cart-item-image rounded"
                ></b-img>

                <!-- Product Details -->
                <div class="cart-item-details ml-3">
                  <!-- Product Name -->
                  <h5 class="mb-1 text-dark font-weight-bold">
                    {{ cartItem.name }}
                  </h5>

                  <!-- Product Price and Discount -->
                  <p class="mb-2">
                    <span class="text-danger font-weight-bold h6"
                      >₹{{ cartItem.price }}</span
                    >
                    <b-badge variant="success" class="ml-2"
                      >{{ cartItem.discount }}% off</b-badge
                    >
                  </p>

                  <!-- Quantity control -->
                  <div class="d-flex align-items-center my-2">
                    <label class="mr-2 font-weight-bold">Qty:</label>
                    <b-form-spinbutton
                      :id="'sb-inline-' + idx"
                      v-model="cartItem.quantity"
                      inline
                      min="1"
                      max="100"
                      size="sm"
                      class="quantity-spinner"
                      @change="updateCartQuantity(cartItem, cartItem.quantity)"
                    ></b-form-spinbutton>

                    <!-- Remove Button -->
                    <b-button
                      variant="outline-danger"
                      size="sm"
                      @click="removeFromCart(cartItem)"
                      class="ml-2 remove-btn"
                      v-b-tooltip.hover.bottom="'Remove item'"
                    >
                      <b-icon icon="trash"></b-icon>
                    </b-button>
                  </div>
                </div>
              </div>
            </b-card>
          </div>

          <!-- No Items Message -->
          <p v-else class="text-center text-muted mt-3 no-items-message">
            Bhaiya please shopping kar ligiye na, garib company hai.
          </p>
        </div>

        <!-- Checkout Button -->
        <b-button
          variant="success"
          class="mt-4 checkout-btn"
          @click="checkoutFn()"
          block
        >
          Check Out
        </b-button>
      </b-modal>
    </div>

    <b-container class="my-4">
      <h3>Electronics Items</h3>
      <!-- Search Function -->
      <b-row>
        <b-col>
          <b-form-input
            v-model="searchQuery"
            placeholder="Search for electronics..."
            class="my-4"
          />
        </b-col>
      </b-row>

      <!-- Electronics Items List -->
      <b-row v-if="filteredItems.length === 0">
        <b-col>
          <h4>Oops! Nothing Found!</h4>
          <p>Try searching for different keywords or check our categories!</p>
        </b-col>
      </b-row>
      <b-row v-else>
        <b-col
          v-for="(item, index) in filteredItems"
          :key="index"
          md="3"
          cols="6"
          sm="6"
          class="my-2"
        >
          <b-card
            :img-src="item.img"
            img-alt="Item image"
            img-top
            class="custom-card shadow-sm"
          >
            <b-card-text>
              <p class="text-truncate mb-1">
                <strong class="text-dark">{{ item.name }}</strong>
              </p>
              <p class="d-flex align-items-center">
                <strong class="text-primary h5">₹{{ item.price }}</strong>
                <b-badge class="ml-2 py-1 px-2" variant="success" pill>
                  {{ item.discount }}% off
                </b-badge>
              </p>
              <p class="text-muted text-truncate">{{ item.description }}</p>

              <!-- Rating of product -->
              <div class="rating d-flex mb-2">
                <span
                  v-for="star in 5"
                  :key="star"
                  class="star"
                  :class="{
                    'text-warning': star <= item.rating,
                    'text-secondary': star > item.rating,
                  }"
                >
                  ★
                </span>
                <small class="ml-2 text-muted">{{ item.rating }} / 5</small>
              </div>
            </b-card-text>

            <b-button
              :variant="isInCart(item) ? 'outline-danger' : 'outline-primary'"
              @click="toggleCartItem(item)"
              size="sm"
              block
              class=""
            >
              {{ isInCart(item) ? "Remove" : "Add to Cart" }}
            </b-button>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchQuery: "",
      items: [
        {
          id: "1",
          quantity: 1,
          name: "Smart TV 55 inch",
          price: 49999,
          discount: 30,
          rating: 5,
          description:
            "High definition 4K Smart TV with amazing picture quality.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "2",
          quantity: 1,
          name: "Wireless Headphones",
          price: 7999,
          discount: 25,
          rating: 3,
          description:
            "Noise-cancelling, wireless headphones with a long-lasting battery.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "3",
          quantity: 1,
          name: "Gaming Laptop",
          price: 12999,
          discount: 15,
          rating: 4,
          description:
            "High-performance gaming laptop with 16GB RAM and 1TB SSD.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "4",
          quantity: 1,
          name: "Bluetooth Speaker",
          price: 2999,
          discount: 20,
          rating: 5,
          description:
            "Portable Bluetooth speaker with deep bass and crystal-clear sound.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "5",
          quantity: 1,
          name: "Smart Watch",
          price: 19999,
          discount: 25,
          rating: 4,
          description:
            "Water-resistant smartwatch with fitness tracking and call features.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "6",
          quantity: 1,
          name: "Tablet 10 inch",
          price: 39999,
          discount: 10,
          rating: 5,
          description:
            "Versatile tablet with a sharp display and long battery life.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "7",
          quantity: 1,
          name: "Smart TV 55 inch",
          price: 49999,
          discount: 30,
          rating: 5,
          description:
            "High definition 4K Smart TV with amazing picture quality.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "8",
          quantity: 1,
          name: "Wireless Headphones",
          price: 7999,
          discount: 25,
          rating: 3,
          description:
            "Noise-cancelling, wireless headphones with a long-lasting battery.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "9",
          quantity: 1,
          name: "Gaming Laptop",
          price: 12999,
          discount: 15,
          rating: 4,
          description:
            "High-performance gaming laptop with 16GB RAM and 1TB SSD.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "10",
          quantity: 1,
          name: "Bluetooth Speaker",
          price: 2999,
          discount: 20,
          rating: 5,
          description:
            "Portable Bluetooth speaker with deep bass and crystal-clear sound.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "11",
          quantity: 1,
          name: "Smart Watch",
          price: 19999,
          discount: 25,
          rating: 4,
          description:
            "Water-resistant smartwatch with fitness tracking and call features.",
          img: "data:image/jpeg;base64,",
        },
        {
          id: "12",
          quantity: 1,
          name: "Tablet 10 inch",
          price: 39999,
          discount: 10,
          rating: 5,
          description:
            "Versatile tablet with a sharp display and long battery life.",
          img: "data:image/jpeg;base64,",
        },
      ],
      cart: [],
    };
  },
  computed: {
    filteredItems() {
      return this.items.filter(
        (item) =>
          item.name.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          item.description
            .toLowerCase()
            .includes(this.searchQuery.toLowerCase())
      );
    },
  },
  mounted() {
    this.loadCartFromLocalStorage(); // Load the cart from localStorage when the component mounts
  },
  methods: {
    isInCart(item) {
      return this.cart.some((cartItem) => cartItem.id === item.id);
    },
    toggleCartItem(item) {
      if (this.isInCart(item)) {
        this.removeFromCart(item);
      } else {
        this.addToCart(item);
      }
    },
    addToCart(item) {
      this.cart.push(item);
      this.saveCartToLocalStorage();
      this.$bvToast.toast(`${item.name} added to the cart!`, {
        title: "Item Added",
        variant: "success",
        autoHideDelay: 2000, // Auto hide after 2 seconds
        solid: true,
        toaster: "b-toaster-bottom-center",
      });

      const existingProduct = this.cart.find(
        (cartItem) => cartItem.id === item.id
      );

      if (existingProduct) {
        existingProduct.quantity += 0; // Increase quantity if the product is already in the cart
      } else {
        // Add new product to the cart with default quantity 1
        this.cart.push({
          ...item,
          quantity: 1,
          img: item.img || "path/to/default/image.jpg", // Use item's img or default image
        });
        this.$bvToast.toast(`${item.name} added to the cart!`, {
          title: "Item Added",
          variant: "success",
          autoHideDelay: 2000,
          solid: true,
          toaster: "b-toaster-bottom-center",
        });
      }

      this.saveCartToLocalStorage();
    },
    updateCartQuantity(item, quantity) {
      // Correct comparison: Find the cart item by comparing the ids
      const cartItem = this.cart.find((cartItem) => cartItem.id === item.id);

      if (cartItem && quantity > 0) {
        cartItem.quantity = quantity; // Update the quantity
        this.saveCartToLocalStorage(); // Save updated cart to localStorage

        this.$bvToast.toast(`${item.name} quantity updated!`, {
          title: "Quantity Updated",
          variant: "info",
          autoHideDelay: 2000,
          solid: true,
          toaster: "b-toaster-bottom-center",
        });
      }
    },
    removeFromCart(item) {
      const index = this.cart.findIndex((cartItem) => cartItem.id === item.id);
      if (index !== -1) {
        this.cart.splice(index, 1);
        this.saveCartToLocalStorage();
      }
      this.$bvToast.toast(`${item.name} removed from the cart!`, {
        title: "Item Removed",
        variant: "danger",
        autoHideDelay: 2000, // Auto hide after 2 seconds
        solid: true,
        toaster: "b-toaster-bottom-center",
      });
    },
    saveCartToLocalStorage() {
      localStorage.setItem("cart", JSON.stringify(this.cart));
    },
    loadCartFromLocalStorage() {
      const cartFromStorage = localStorage.getItem("cart");
      if (cartFromStorage) {
        this.cart = JSON.parse(cartFromStorage);
      }
    },
    checkoutFn() {
      this.$router.push("/checkout");
    },
  },
};
</script>

<style scoped>
.custom-card img {
  width: 100px;
  height: 100px;
  margin-left: 40px;
}
.cart-item-image {
  width: 80px;
  height: 80px;
  object-fit: cover;
}
</style>
